


























import { Component, Vue, Watch, Prop } from 'vue-property-decorator';
import SvgIcon from '@/components/utilities/SvgIcon.vue';
import CodeInput from 'vue-verification-code-input';

@Component({
    components: {
        SvgIcon,
        CodeInput,
    },
})
export default class Timer extends Vue {
        @Prop() timeLimit: number;
        FULL_DASH_ARRAY = 283;
        WARNING_THRESHOLD = 60;
        ALERT_THRESHOLD = 30;

        COLOR_CODES = {
            info: {
                color: "green"
            },
            warning: {
                color: "orange",
                threshold: 0
            },
            alert: {
                color: "red",
                threshold: 0
            }
        };

      TIME_LIMIT = 10;
      timePassed= 0;
      timerInterval= null
      finishedTimer = false;
      correctTimer = false;
     @Prop() isCorrect: boolean;

    get circleDasharray() {
      return `${(this.timeFraction * this.FULL_DASH_ARRAY).toFixed(0)} 283`;
    }

    get formattedTimeLeft() {
      const timeLeft = this.timeLeft;
      const minutes = Math.floor(timeLeft / 60);
      let seconds: number | string = timeLeft % 60;
      if (seconds < 10) seconds = `0${seconds}`;
      return `${minutes}:${seconds}`;
    }

    get timeLeft() {
      return this.TIME_LIMIT - this.timePassed;
    }

    get timeFraction() {
      const rawTimeFraction = this.timeLeft / this.TIME_LIMIT;
      return rawTimeFraction - (1 / this.TIME_LIMIT) * (1 - rawTimeFraction);
    }

    get remainingPathColor() {
      const { alert, warning, info } = this.COLOR_CODES;
     // if (this.timeLeft <= 0) return 'none'
       if (this.timeLeft <= alert.threshold) {
        return alert.color;
      } else if (this.timeLeft <= warning.threshold) {
        return warning.color;
      } else {
        return info.color;
      }
    }

    @Watch('timeLeft')
    watchTimeLeft(newValue) {
      if (newValue === 0) {
        this.onTimesUp();
        this.$emit('showExpiredText')
      }
    }

    mounted() {
        this.TIME_LIMIT = this.timeLimit
        this.ALERT_THRESHOLD =this.TIME_LIMIT / 3;
        this.WARNING_THRESHOLD=this.TIME_LIMIT / 2;
        this.COLOR_CODES.warning.threshold=this.WARNING_THRESHOLD;
        this.COLOR_CODES.alert.threshold=this.ALERT_THRESHOLD;
        this.startTimer();       
    }

    reset(){
        this.onTimesUp();
        this.timePassed = 0;
        this.TIME_LIMIT = this.timeLimit;
        this.startTimer();
    }

    onTimesUp() {
      clearInterval(this.timerInterval);
    }

    startTimer() {
      this.timerInterval = setInterval(() => (this.timePassed += 1), 1000);
    }

    @Watch('isCorrect')
    isCorrectCode(){
        if (this.isCorrect === true){
        this.correctTimer = true;
        this.onTimesUp();
        this.timerInterval = setInterval(() => (this.timePassed -= 1), 5);}
    }
  
    get labelClass(){
       let labelClass = 'base-timer__label';
       let showClass = this.timeLeft <=0 ? 'red' : '';
       return labelClass + ' ' + showClass
    }

};
